.carousel {
    overflow: hidden;
    margin-left: 300px;
    padding-bottom: 50px;
    border-bottom: 1px dashed #333;
    background: #485563;
    background-image: linear-gradient(to right, #29323c, #485563);
    color: #f1f1f1;
    position: relative;
}
.inner {
    white-space: nowrap;
    transition: transform 0.3s;
}
.carousel-item {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: auto;
    padding-bottom: 100px;
    color: #fff;
    z-index: -1;
}

.indicators {
    display: flex;
    justify-content: center;
    position: absolute;
    width: 100%;
    margin: 0 auto 100px auto;

    button {
        margin: 5px;

        &.active {
            background: green;
            color: #fff;
        }
    }
}